<template>
    <div class="star" :class="starType">
        <span class="star-item" :class="item" v-for="(item,index) in starClasses" :key="index"></span>
    </div>
</template>

<script>
const LENGTH = 5;   //默认显示5颗星
const CLS_ON = "on"
const CLS_OFF = "off"
const CLS_HALF = "half"

export default {
    

    //接收父组件传递过来的数据
    props:{
        score:Number,
        size:Number
    },
    //计算属性
    computed:{
        //根据分值显示对应的星星，       
        starClasses(){

            let result = [];  //保存星星

            let starScore = Math.floor(this.score * 2) / 2;  
            
            let integer = Math.floor(starScore);  //3颗全亮的
            for(let i=0;i<integer;i++){
                result.push(CLS_ON);
            }

            //半亮的星星,判断是否有小数，有小数显示一颗半亮的
            let hasDecimal = starScore % 1 != 0; //有小数
            if(hasDecimal){
                result.push(CLS_HALF);
            }

            //剩下的显示不亮的
            while(result.length < LENGTH){
                result.push(CLS_OFF);
            }

            return result;
        },
        starType(){
            return "star-"+this.size;
        }
    }
}
</script>

<style lang="less" scoped>
    .star{        
        &.star-24{
            .star-item{
                width: 10px;
                height: 10px;
                display: inline-block;  /*span标签的宽高有效了*/
                background-repeat: no-repeat;     
                background-size: 10px 10px; /*背景尺寸*/
                &.on{
                    background-image: url("star24_on@2x.png")
                }
                &.off{
                    background-image: url("star24_off@2x.png")
                }
                &.half{
                    background-image: url("star24_half@2x.png")
                }
            }
        }
       &.star-36{
            .star-item{
                width: 15px;
                height: 15px;
                margin-right: 6px;
                background-size: 15px 15px;
                &:last-child{
                    margin-right: 0;
                }
                &.on{
                    background-image:url('star36_on@2x.png')
                }
                &.half{
                    background-image:url('star36_half@2x.png')
                }
                &.off{
                    background-image:url('star36_off@2x.png')
                }
            }
        }
        &.star-48{
            .star-item{
                width: 20px;
                height: 20px;
                margin-right: 9px;
                background-size: 20px 20px;
                &:last-child{
                    margin-right: 0;
                }
                background-size: 20px 20px;
                &.on{
                    background-image:url('star48_on@2x.png')
                }
                &.half{
                    background-image:url('star48_half@2x.png')
                }
                &.off{
                    background-image:url('star48_off@2x.png')
                }
            }
        }
    }
</style>




